import "./Headers.css"
import {Link} from "react-router-dom";


const Header = () => {
    return (
        <div className="Header">
            <div className="header-left">
                <Link to="/">
                    <img className="header-logo"
                         src="data:image/png;base64,"/>
                </Link>
            </div>

            {/*<div className="header-search">*/}
            {/*    <input className="search-input" type="text" placeholder=""/>*/}
            {/*</div>*/}

            <div className="header-right">
                <Link to="/" className="header-content">Home</Link>
                <Link to="/contacts" className="header-content">Contacts</Link>
                <Link to="/about" className="header-content">About</Link>
                {/*<h4 className="header-content">Login</h4>*/}
                <span className="material-icons">
                    login
                </span>
            </div>
        </div>
    )

}
export default Header

