import React, {useEffect, useState} from "react";
import {Link, useParams} from "react-router-dom";
import {api} from "../../base/axios";
import "./Competitions.css"
import {Spinner, Table} from "react-bootstrap";

const ball = "data:image/jpeg;base64,"

export const CompetitionsIn = () => {
    const {id} = useParams();
    const [teams, setTeams] = useState([]);
    const [table, setTable] = useState([]);
    const [loader, setLoader] = useState(true);

    const getTeams = () => {
        setLoader(true)
        api.get(`/competitions/${id}/teams`).then((res) => {
                setTeams(res.data.teams)
                setLoader(false)

            }
        )
    }

    const getStandings = () => {
        api.get(`/competitions/${id}/standings`).then((res) => {
                setTable(res.data.standings[0].table)
            }
        )
    }

    useEffect(() => {
        getTeams()
        getStandings()
    }, []);


    return (
        <div className="">
                <div className={"teams-tittle"}>Soccer Clubs</div>
            {loader ? (
                    <div className="d-flex justify-content-center align-items-center mt-5">
                        <Spinner animation="border" variant="success"/>
                    </div>
                ) : (
                  <div className="row">
                      {teams.map((item) => (
                          <div className={"mt-4 col-3"}>
                              <div key={item.id} className={""}>
                                  <div className="item-teams " style={{fontWeight: "bolder"}}>
                                      <img className="logo-teams "
                                           style={{marginRight: 15, fontFamily: 'Quicksand', textDecoration: "none"}}
                                           src={item.crestUrl || ball} alt="logo-club"/>
                                      <Link to={`/team/${item.id}`}  className="link">
                                          {item.name}
                                      </Link>
                                  </div>
                              </div>
                          </div>

                      ))
                      }
                  </div>
            )}
            {table.length > 0 && (
                <Table className="mt-4 " striped bordered hover>
                    <thead>
                    <tr>
                        <th>#</th>
                        <th></th>
                        <th ></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                    </thead>
                    <tbody>
                    {table.map((item, index) => (
                        <tr key={index}>
                            <th>{item.position}</th>
                            <th>
                                <img className={"logo-teams-table"} src={item.team.crestUrl}/>
                            </th>
                            <th>{item.team.name}</th>
                            <th>{item.playedGames}</th>
                            <th>{item.won}</th>
                            <th>{item.lost}</th>
                            <th>{item.draw}</th>
                            <th>{item.goalsFor}</th>
                            <th>{item.goalsAgainst}</th>
                            <th>{item.points}</th>
                        </tr>
                    ))}
                    </tbody>
                </Table>
            )}
        </div>
    )
}
